// Beta testing 
#beta-user-testing {
width: 100%;
float: left;
padding: 10px;
border: 2px solid #005ea5;	
margin: 10px 0;
box-sizing: border-box;
p {
margin: 0 !important;
}
}

#beta-user-testing a:link, #beta-user-testing a:visited, #beta-user-testing a:hover, #beta-user-testing a:focus {
color: #005ea5 !important;	
}

#beta-user-testing p {
font-size: 16px;
margin: 0px;
}

#contentContainer .topTaskSide .multistep {
width: 100%;
}

// Pagination 

#pagination {
width: 100%;
float: left;
}

#contentContainer #pagination ul, #contentContainer #pagination ul li {
float: left;
background: none;
margin: 0 !important;
padding: 0;
}

#contentContainer #pagination ul span {
float: left;
display: inline-block;
clear: both;
}

#pagination .next-page, #pagination .previous-page {
width: 50%;
float: left;
@media only screen and (max-width : 991px) {
width: 100%;
}
}

#pagination .next-page a, #pagination .previous-page a {
width: 100%;
padding: 10px;
box-sizing: border-box;
float: left;
}

#pagination span {
color: $red;
}

#pagination a:hover {
background: $gray;
}

.pagination-icon {
font-size: 2.167em;
}

.pagination-text {
font-size: 1.167em;
padding-top: 10px;
}

#contentContainer #pagination .next-page .pagination-icon::after {	
color: $red;
content: "\f061";
font-family: 'Font Awesome 5 Free';
font-weight: 900;
left: 0; 
padding-right: 5px;
position: relative;
top: 0;
}

#contentContainer #pagination .previous-page .pagination-icon::after {	
color: $red;
content: "\f060";
float: left;
font-family: 'Font Awesome 5 Free';
font-weight: 900;
left: 0; 
padding-right: 5px;
position: relative;
top: 0;
}

// Aside

.exploreTopic {
width: 100%;
float: left;
box-sizing: border-box;
padding: 10px;
margin-bottom: 15px;
}