// Background colours

.backgroundRed {
background: $red !important;
}

.backgroundGray {
background: $gray !important;
}

// More links

p.moreLinkSide {
clear: both;	
}

.columnFull p.moreLink {
font-weight: 700;
clear: both;
margin: 0 !important;
margin-top: auto !important;
}

.moreFlex {
display: -webkit-box;  
display: -ms-flexbox; 
display: -webkit-flex;
display: flex;        
flex-flow: column;
}

p.moreLink a {
padding: 0;
}

.backgroundRed a, .backgroundRed h2, .backgroundRed h3, .backgroundRed p {
color: white !important;
}

/* News */

#contentContainer .listLinks a {
text-decoration: none;
}

#contentContainer .listLinks a:hover {
text-decoration: underline;
}

// Subscribe box

.subscribe img {
display: inline-block;
float: left;
}

.subscribe h2:before {
content:url('../../assets/images/icons/emaili.png'); 
position:relative;
z-index:100000;
left:0;
top: 5px;
padding-right: 10px;
}

.subscribe .eBulletinButton {
width: auto;
font-size: 1em;
}

.subscribe label {
color: #4E4E4E;
}

.subscribe input[type=submit] {
padding:5px 15px;
margin-top: 10px;
background:#ccc; 
border:0 none;
cursor:pointer;
-webkit-border-radius: 5px;
border-radius: 5px;
background: #a61333;
color: white;
}

// Secondary search

#secondarySearch {
width: 100%;
margin-top: 20px;
float: left;
}

#secondarySearch table {
width: 100% !important;
margin: 0;
}

#secondarySearch table td {
border: none;
padding: 0;
}

#secondarySearch table .gsc-input-box {
border: none;
border-color: none;
}

#secondarySearch table .gsc-input {
background: $gray !important;
border: none;
text-indent: 20px !important;
}

#secondarySearch table form.gsc-search-box {
margin-bottom: 0 !important;
}

#secondarySearch table .gsc-search-button .gsc-search-button-v2 {
background: $red;
}

#secondarySearch table .gsc-search-button-v2 svg {
fill: white !important;
}