// Mobile first

#contentContainer * {
-webkit-box-sizing: border-box;
-moz-box-sizing: border-box;
box-sizing: border-box;
}

#pageContainer {
width: 90%;
float: left;
padding: 0px;
padding-right: 5%;
padding-left: 5%;
}

// Column gutter

.column {
margin: 0 1.5% 0 0;
}

// Box layout

.box {
width: 100%;
border-radius: 0;
margin-bottom: 15px;
}

.box .more {
width: 100%;
position: relative;
}

.more {
border-radius: 0;
}

/* Spacing & Borders */

#contentContainer .border {
padding-bottom: 10px;
border-bottom: 2px solid $red;
}


#contentContainer .columnFull {
width: 100%;
float: left;
height: auto;
clear: both;
padding-bottom: 20px;
padding-top: 20px;
}

#contentContainer {
width: 100%;
.oneThird, .twoThird, .twoThirdHalf, .oneThird.gallery, .mostPopular, .homepageBanner {
width: 100%;
}
}



/* Layout borders */

#contentContainer .border-top {
border-top: 2px solid $gray;
border-radius: 0;
}

#contentContainer .border-top-red {
border-top: 2px solid $red;
border-radius: 0;
} 

#contentContainer .border-right {
border-right: 2px solid $gray;
}



/* Content types */

.last {
margin-right: 0 !important;
}

.noBoxPadding .column {
padding-left: 0 !important;
padding-right: 0 !important;
}

.noBoxPadding.column {
padding-left: 0;
padding-right: 0;
}

.column {
float: left;
position: relative;
padding: 10px;
}

#beta-user-testing {
width: 100%;
}



