// Off-canvas

#off-canvas-container {
padding-top: 10px;
}

#off-canvas-menu {
float: left;
width: 100%;
clear: both;
font-size: 1rem;
}

#off-canvas-menu li {
width: 100%;
float: left;
list-style-type: none;
background: none;
padding: 0;
}

#off-canvas-menu li a {
float: left;
width: 100%;
color: white;
}


.responsive-nav-container a.js-offcanvas-close {
float: right;
padding: 10px 15px 10px 15px;
}

.responsive-nav-container #off-canvas-menu li a {
padding: 10px 5% 10px 5%;
}

.responsive-nav-container #off-canvas-menu li:hover {
background: $black;
}

#triggerButton span {
color: white;
}

#triggerButton {
float: right;
padding: 5px 10px 5px 10px;
}

#triggerButton:hover i {
color: white;
}

.off-canvas-close {
float: left;
padding-right: 0%;
padding-left: 5%;
width: 95%;
}

.off-canvas-close a:hover {
cursor: pointer;
}

#offCanvas {
border-bottom: 3px solid #999;
background: $red;
}

.full-width-container {
width: 100%;
float: left;
clear: both;
}

.responsive-container {
width: 90%;
padding-left: 5%;
padding-right: 5%;
}

.responsive-nav-container {
width: 100%;
}
