// BASE

// Colours 

$gray: #f3f3f3;
$red: #981e32;
$darkred: #58041b;
$black: #333333;

// Font awesome icons 
li.famr {
margin-right: 10px;
}

// Links 

#contentContainer {
a:link,
a:visited,
a {
color: $red;
text-decoration: none;
}

a:hover,
a:active,
a:hover,
a:focus
{
text-decoration: underline;
}
}

// Featured image links 

.gallery {
a:link h2,
a:link h3 {
color: $red !important;
}
}

// Margins - Padding - Borders

#contentContainer {

.noPadding {
padding: 0 !important;
}

.border:last-of-type {
border-bottom: none;
}

}

// Lists 

.backgroundRed ul.listCaret li:before {
color: white;
}

#contentContainer .columnFull ul.listCaret {
margin-bottom: 0 !important;
}

#contentContainer .columnFull  ul.listCaret li {
background-image: none;
padding-left: 15px;
position: relative;
}


#contentContainer .columnFull .backgroundRed p.moreLink::before {
color: white;
}

#contentContainer ol, #contentContainer ul {
padding-left: 0;
margin: 0;
}

#contentContainer ul {
margin: 0 0 15px 10px !important;
}

#contentContainer .columnFull ul.multistep {
margin: 0 0 10px 5px !important;
}

#contentContainer .columnFull ul.listLinks {
margin: 0 0 15px 0px !important;
float: left;
}

#contentContainer .columnFull ul.articleList {
margin-bottom: 0 !important;	
}

#contentContainer ol.multistep li {
margin-left: 15px;
padding: 0;
}

#contentContainer ul.multistep li {
margin-left: 10px;
}

#contentContainer ul.linkArrow li {
padding-left: 0;
}

#contentContainer .linkArrow li {
display: block; 
}

#contentContainer .linkArrow li a {
text-decoration: none !important; 
}

#contentContainer .linkArrow li a:hover {
text-decoration: underline !important; 
}

@media only screen and (min-width : 992px) {

.linkArrow li {
display: block;
}	

}



#contentContainer ul.itemThumbBook li {
float: left;
padding: 0 !important;
margin-bottom: 10px;
width: 100%;
}

#contentContainer ul.itemThumbBook li p {
margin: 0 !important;
}

#contentContainer ul.itemThumbBook span {
padding-right: 0;
}

#contentContainer ul.listLinks li
{
background: none;
padding-left: 0;
list-style-type: none;
margin: 0;
clear: both;
float: left;
}

#contentContainer ul.mostPopular li {
font-weight: 700;
}

#contentContainer .columnFull .column.noBoxPadding ul.listLinks {
margin-bottom: 10px !important;
}

#contentContainer ul.listLinks li a {
float: left;
}

#contentContainer ul.listLinks li span {
display: inline-block;
}

#contentContainer ul.listLinks p {
clear:both;
}

// Article lists

#contentContainer .article {
padding-top: 0;
padding-bottom: 0;
}

#contentContainer ul.guidanceList, #contentContainer ul.articleList {
font-size: inherit;
float: left;
}

#contentContainer ul.guidanceList li, ul.articleList li {
list-style-type: none;
background: none;
padding: 0;
@media only screen and (min-width : 980px) {
min-height: 87px;
margin-bottom: 10px;
clear: both;
float: left;
}
}

#contentContainer .columnFull ul {
margin: 0 0 15px 0px !important;
}

.columnFull .guidanceList li {
margin-bottom: 20px !important;
}

#contentContainer ul.list li {
background: none;
list-style-type: none;
padding: 0;	
}

#contentContainer .mb0 {
margin-bottom: 0;	
}

// Main content 

#contentContainer ol {
margin-bottom: 0;
}

#contentContainer .pressReleases li {
padding-bottom: 20px;	
}

#contentContainer .safetyAlerts li {
list-style-type: none;
}


// Forms 

input#email {
box-sizing: border-box;
padding: 5px;
}

.eBulletinButton {
background: $red;
color: white;
padding: 5px;
margin-top: 10px;
}

// Responsive images 

img.featuredImg {
width: 100%;
@media only screen and (min-width : 980px) {
min-height: 200px;
}
height: auto;
float: left;
margin-bottom: 15px;
}

img.responsiveImg {
width: 100%;
float: left;
}
