/* Media queries & Responsive design */


// Equal heights fix

@media only screen and (max-width : 991px) {
/* Disables auto height on mobile devices */
.equalHeightsRow1, .equalHeightsRow2, .equalHeightsRow3, .equalHeightsRow4, .equalHeightsRow5, .equalHeightsRow6, .equalHeightsRow7  {
height: auto !important;	
}
}

// equalHeight class for homepage banner
@media only screen and (max-width : 980px) {
	.equalHeights20 {
	height: auto !important;		
	}
}
/* Content */



/* Small Devices, Tablets */
@media only screen and (min-width : 639px) {

#contentContainer .oneThird.gallery {
width: 32.26%;
}

}

/* Small Devices, Tablets */
@media only screen and (min-width : 768px) {



#contentContainer .twoThirdHalf {
width: 49.2%;
}
	

}	

/* Medium Devices, Desktops */
@media only screen and (min-width : 992px) {
	
#contentContainer .oneThird {
width: 32.26%;
}
	
#contentContainer .twoThird {
width: 66.13%;
}
	

	
	
}

