
/* Desktop */
@media only screen and (min-width : 980px) {

#headerContainer {
min-width: 960px;
}

#header {
max-width: 940px;
width: 940px;
padding-left: 0;
padding-right: 0;
}

#pageContainer {
width: 940px;
float: none;
padding-left: 0;
padding-right: 0;
}

#contentContainer {
margin: 0;
width: 940px;
}



/* Layout */

#contentContainer .oneThird {
width: 300px;
margin-right: 20px;
}

#contentContainer .twoThird {
width: 620px;
margin-right: 20px;
}

#contentContainer .mostPopular {
width: 280px;
margin-right: 20px;
}

#contentContainer .homepageBanner {
width: 640px;
margin-right: 20px;
}

#contentContainer .twoThird.noBoxPadding  {
width: 620px !important;	
}

#contentContainer .noPadding.twoThird {
width: 620px;	
}

#contentContainer .twoThirdHalf {
width: 290px;
margin-right: 20px;
}

#contentContainer .twoThirdHalf.subColumn {
width: 290px;
padding: 0;
}

#contentContainer .noBoxPadding .twoThirdHalf.subColumn {
width: 300px;
}

#contentContainer .noBoxPadding.twoThirdHalf {
width: 300px;
}

#contentContainer .columnFull {
width: 940px;
}

.multistep {
width: 280px;
float: left;
}

/* No box padding */

#contentContainer .noBoxPadding.oneThird {
width: 300px;
margin-right: 20px;
}

#contentContainer .noBoxPadding .twoThird {
width: 620px;
margin-right: 20px;
}

#contentContainer .noBoxPadding .multistep {
width: 308px;
float: left;
}

/* Offset */

.offsetTwoThird {
margin-left: 640px !important;
}


/* Beta */

#beta-user-testing {
width: 940px;
}

#globalSearch {
float: right;	
}
#globalSearch table {
width: 300px;	
}


.beta-highlight {
margin-left: 0;
}

#off-canvas-container {
display: none;
}

.offCanvasHeaderLogo {
display: none;
}


}
